.victoryMessage{transition:opacity ease 300ms;opacity:1}.victoryMessage--hidden{cursor:default;opacity:0}

.card{height:150px;transition:300ms transform ease, 300ms opacity ease, 300ms box-shadow ease;perspective:900px;z-index:0;position:relative;cursor:pointer;font-size:2rem}.card:hover{transform:scale(1.1);z-index:100}.card:focus{transform:scale(1.05);outline:none}.card.card--selected{transform:scale(1.1);z-index:200}.card.card--matched{opacity:0}.card__inner{box-shadow:var(--shadow);transition:300ms transform ease, 300ms opacity ease, 300ms box-shadow ease;transform-style:preserve-3d;height:100%;border-radius:var(--border-radius)}.card__inner:hover{box-shadow:var(--shadow-hover)}.card--selected .card__inner,.card--matched .card__inner{transform:rotateY(180deg);box-shadow:var(--shadow-hover)}.card--matched{cursor:default}.card--matched .card__inner{transform:rotateY(180deg) translateY(-1rem)}.card__front,.card__back{position:absolute;border-radius:var(--border-radius);height:100%;width:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:flex;align-items:center;justify-content:center}.card__back{background:var(--deep-blue);font-size:5rem;color:rgba(255,255,255,0.8)}.card__front{background:#fff;transform:rotateY(180deg);z-index:900}

.cardGrid{display:grid;grid-template-columns:repeat(3, 1fr);grid-auto-rows:auto;grid-gap:1rem;margin:0 auto;padding:1rem}@media (min-width: 600px){.cardGrid{width:clamp(600px, 800px, 70vw);grid-template-columns:repeat(6, 1fr)}}.resetContainer{padding:1.25rem;position:absolute;left:50%;transform:translateX(-50%);z-index:900}.btn{cursor:pointer;transition:opacity ease 300ms;padding:15px 20px;background:var(--med-blue);color:#fff;border:none;-webkit-animation:pulse 2s infinite;animation:pulse 2s infinite}.btn[disabled]{cursor:default;opacity:0}@-webkit-keyframes pulse{0%{box-shadow:var(--shadow);background:var(--orange)}70%{box-shadow:var(--shadow-hover);background:var(--orangered)}100%{box-shadow:var(--shadow);background:var(--orange)}}@keyframes pulse{0%{box-shadow:var(--shadow);background:var(--orange)}70%{box-shadow:var(--shadow-hover);background:var(--orangered)}100%{box-shadow:var(--shadow);background:var(--orange)}}

:root{--cool-gray: #F2F8FF;--dark-gray: #444;--med-blue: #496A81;--deep-blue: #2B3A67;--orange: #F68E5F;--orangered: #F76C5E;--fnt: 'Roboto', 'Helvetica Neue','Helvetica', arial, sans-serif;--shadow:0 4px 8px -4px rgba(0,0,0, .3);--shadow-hover: 0 8px 24px rgba(0,0,0,.1);--border-radius: 5px}*{box-sizing:border-box}.App{text-align:center;min-height:100vh;display:flex;flex-direction:column;justify-content:center}body{background-color:#F2F8FF;background-color:var(--cool-gray);margin:0;font-weight:500;color:#444;color:var(--dark-gray);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif}h1{text-transform:uppercase}h1,h2,h3{font-family:'Roboto', 'Helvetica Neue','Helvetica', arial, sans-serif;font-family:var(--fnt);font-weight:300}

